import { Component, OnInit, Output,EventEmitter  } from '@angular/core';
import { SearchReportService } from 'src/app/service/search-report.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  searchdata = '';
  showSearch = false;
  constructor(private searchReportService : SearchReportService) { }

  ngOnInit() {
    this.searchReportService.getShowSearch$()
          .subscribe(data => this.showSearch=data)
  }

  searchReport() {
    this.searchReportService.setSearchValue(this.searchdata);
  }
}

