import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SearchReportService } from 'src/app/service/search-report.service';

@Component({
  selector: 'app-report-display',
  templateUrl: './report-display.component.html',
  styleUrls: ['./report-display.component.css']
})
export class ReportDisplayComponent implements OnInit {
  
  reportServer: string = 'http://nstn-lt-050/ReportServer';
  reportUrl: string = 'Majesco/Majesco';
  showParameters: string = "true"; 
  language: string = "en-us";
  width: number = 100;
  height: number = 100;
  toolbar: string = "true";
  constructor(private route: ActivatedRoute,private searchReportService : SearchReportService) { }

  ngOnInit() {
    this.reportUrl = this.route.snapshot.params.foldername + "/" + this.route.snapshot.params.reportname;
    this.searchReportService.setShowSearch(false);
  }

}
