import { pipe } from "rxjs";
import { SelectorMatcher } from '@angular/compiler';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name : 'searchpipe'
})
export class searchPipe implements PipeTransform {
    transform(values : any[],search : string,key : string) {
        if(!values || !search) {
            return values;
        }
        return values.filter(value => value[key].toLowerCase().indexOf(search.toLowerCase()) !== -1);
    }
}