import { Component, OnInit } from '@angular/core';
import { SearchReportService } from 'src/app/service/search-report.service';
import * as reportDetails from '../../report/reportDetails.json';

@Component({
  selector: 'app-report-list',
  templateUrl: './report-list.component.html',
  styleUrls: ['./report-list.component.css']
})
export class ReportListComponent implements OnInit {

  config: any;
  reportLists : any = (reportDetails as any).default;
  searchdata = '';
  key = 'reportName';
  constructor(private searchReportService : SearchReportService) { 
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.reportLists.count
    };
   }

  ngOnInit() {
    this.searchReportService.getSearchValue$()
            .subscribe(data =>  this.searchdata = data);
    this.searchReportService.setShowSearch(true);
  }

  pageChanged(event){
    this.config.currentPage = event;
  }

}
