import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable()
export class SearchReportService {

    searchValue = new Subject<string>();
    showSearch = new Subject<boolean>();
    constructor() { };

    getSearchValue$(): Observable<string> {
        return this.searchValue.asObservable();
    }

    setSearchValue(searchData: string): void {
        this.searchValue.next(searchData);
    }

    getShowSearch$(): Observable<boolean> {
        return this.showSearch.asObservable();
    }

    setShowSearch(enable: boolean): void {
        this.showSearch.next(enable);
    }

}